<template>
  <v-row>
    <v-col
      v-for="image in images"
      :key="image.id"
      cols="6"
      sm="3"
      md="2"
      lg="2"
    >
      <ImageDelete
        :image="'http://a0604518.xsph.ru/' + image.img"
        @delete="handleDelete(image)"
      />
    </v-col>
    <v-col
      v-for="item in uploadImagesCount"
      :key="item.id"
      cols="6"
      sm="3"
      md="2"
      lg="2"
    >
      <ImageUpload
        @change="handleImageUpload"
        @delete="deleteUploadedImage($event, item.id)"
      />
    </v-col>
  </v-row>
</template>

<script>
import ImageUpload from "@/components/image-upload/ImageUpload.vue";
import ImageDelete from "@/components/image-delete/ImageDelete.vue";

export default {
  name: "EditImages",

  data() {
    return {
      images: this.initialImages,
      uploadedImages: [],
      uploadImagesCount: [{id: Date.now()}]
    }
  },

  components: {
    ImageUpload,
    ImageDelete
  },

  methods: {
    async handleDelete(imageToDelete) {
      this.images = this.images.filter(image => image.id !== imageToDelete.id);
      // Make request to delete from server too
      const res = await fetch(`http://a0604518.xsph.ru/api/photos/destroy/${imageToDelete.id}`, {
        method: 'POST',
      });
      const data = await res.json();
    },

    handleImageUpload(payload) {
      this.uploadedImages.push(payload);
      this.uploadImagesCount.push({id: Date.now()});
      this.$emit("change", this.uploadedImages);
    },

    deleteUploadedImage(fileId, id) {
      this.uploadImagesCount = this.uploadImagesCount.filter((item) => (
        item.id !== id
      ));

      this.uploadedImages = this.uploadedImages.filter(item => (
        item.id !== fileId
      ));

      this.$emit("change", this.uploadedImages);
    }
  },

  watch: {
    initialImages(newImages) {
      this.images = newImages;
    }
  },

  props: {
    initialImages: {
      type: Array,
      required: false
    }
  }
}
</script>
