<template>
  <v-card class="pa-4">
    <EditImages
      :initialImages="images"
      @change="handleChange"
    />
    <v-btn
      class="my-10"
      depressed
      color="primary"
      @click="upload"
    >
      Primary
    </v-btn>
  </v-card>
</template>

<script>
import EditImages from "@/components/edit-images/EditImages.vue";

export default {
  name: "Images",

  data() {
    return {
      images: [],
      imagestoUpload: []
    }
  },

  methods: {
    handleChange(images) {
      this.imagestoUpload = images.map(image => image.image);
    },

    async getPhotos() {
      const response = await fetch('http://a0604518.xsph.ru/api/photos');
      const data = await response.json();
      this.images = data.photos;
    },

    async upload() {
      const formData = new FormData();

      this.imagestoUpload.forEach(image => {
        formData.append("img[]", image);
      })

      const res = await fetch('http://a0604518.xsph.ru/api/photos/store', {
        method: 'POST',
        body: formData
      });
      const data = await res.json();
      console.log(data);
    }
  },

  mounted() {
    this.getPhotos();
  },

  components: {
    EditImages
  }
}

</script>
