<template>
  <div class="file-upload" :class="{'theme--dark': $vuetify.theme.isDark}">

    <div
      class="file-upload__wrap"
      v-if="!image"
      :class="{'image-dropping': dragging}"
      @dragover="dragging = true"
      @dragleave="dragging = false"
    >
      <input class="file-upload__input" type='file' accept="image/*" @change="readURL" />
      <div class="drag-text">
        <v-icon large>{{ icons.mdiFileUpload }}</v-icon>
        <div class="my-4"></div>
        <p>Rasm qoâ€™shish</p>
      </div>
    </div>

    <ImageDelete
      v-else
      :image="image"
      @delete="handleRemove"
    />

  </div>
</template>

<script>
import { mdiFileUpload } from '@mdi/js';
import ImageDelete from "@/components/image-delete/ImageDelete.vue";


export default {
  name: "ImageUpload",

  components: {
    ImageDelete
  },

  data() {
    return {
      image: null,
      dragging: false,
      fileId: Date.now(),

      icons: {
        mdiFileUpload
      }
    }
  },

  methods: {
    readURL(e) {
      const input = e.target;

      if (input.files && input.files[0]) {
        this.$emit("change", {id: this.fileId, image: input.files[0]});

        const reader = new FileReader();

        reader.readAsDataURL(input.files[0]);

        reader.onload = (e) => {
          this.image = e.target.result;
        };
      }
    },

    handleRemove() {
      this.image = null;
      this.$emit("delete", this.fileId);
    }
  }
}
</script>

